<template>
  <div class="scroll">
    <div id="plat_box">
      <div
        v-for="(item,i) in platAll"
        :key="i"
        class="content"
      >
        <div>
          <img
            :src="platPic[item.dictValue]"
            style="margin:10px 0;height:60px;"
          />
          <div>
            <div v-if="item.dictStatu == '0'">请等待审核</div>
            <div v-else-if="item.dictStatu == '1'">自动接单中</div>
            <div
              v-else
              style="height: 30px;"
            ></div>
            <!-- 按钮 -->
            <div
              v-if="hotelTypes && item.dictStatu == '0' || hotelTypes && item.dictStatu == '1'"
              @click="goNext('/relation',item)"
              :class="item.dictStatu == '0' ? 'bbb' : 'aaa'"
            >{{ hotelTypes[item.dictStatu] }}</div>
            <div
              v-else
              class='ccc'
              @click="applyOpen(item)"
            >申请开通</div>
          </div>
        </div>
      </div>
    </div>
    <van-dialog
      v-model="show_apply"
      :before-close="confirmApply"
      title="输入账号信息"
      show-cancel-button
    >
      <van-field
        v-model="dataform_add.otaUserName"
        label="登陆账号"
        title-style='color:red;line-height:40px;'
        placeholder="请输入登陆账号"
      />
      <van-field
        v-model="dataform_add.otaPassWord"
        label="登陆密码"
        title-style='color:red;line-height:40px;'
        placeholder="请输入登陆密码"
      />
      <van-field
        v-if="dataform_add.otaSource == '19'"
        v-model="dataform_add.hotelOtaCode"
        label="OTA酒店id"
        title-style='color:red;line-height:40px;'
        placeholder="请输入OTA酒店id"
      />
    </van-dialog>
  </div>
</template>
<script>
import { Toast } from 'vant';
import { GetSystemDictDataByType } from '@/api/api_query';
import { PostBusinessHotelOtaAccountByhotel, PostBusinessHotelOtaAccountAdd } from '@/api/api_ota';
export default {
    data() {
        return {
            platAll: [],
            hotelTypes: {},
            platPic: {
                6: require('@/assets/outPalt/mt.png'),
                8: require('@/assets/outPalt/tj.png'),
                9: require('@/assets/outPalt/apy.png'),
                17: require('@/assets/outPalt/fz.png'),
                19: require('@/assets/outPalt/xc.png')
            },
            dataform_add: {
                hotelId: this.$route.query.hotelId,
                hotelName: null,
                otaSource: null,
                otaSourceName: null,
                otaUserName: null,
                otaPassWord: null,
                hotelOtaCode: null
            },
            show_apply: false
        };
    },
    created() {
        this.$store.commit('setHeader', '直连管理');
        this.getStatus();
    },
    methods: {
        // 查询酒店OTA账户
        getStatus() {
            // 状态
            GetSystemDictDataByType({ dictType: 'business_ota_relation_status' }).done((res) => {
                res.data.map((item) => {
                    this.hotelTypes[item.dictValue] = item.dictLabel;
                });
                PostBusinessHotelOtaAccountByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                    this.getType(res.data);
                });
            });
        },
        // 平台
        getType(statusArr) {
            GetSystemDictDataByType({ dictType: 'business_ota_source' }).done((res) => {
                res.data.map((item) => {
                    item.dictStatu = null;
                    statusArr.map((itm) => {
                        if (item.dictValue == itm.otaSource) {
                            item.dictStatu = itm.status;
                            item.hotelOtaRelationId = itm.id;
                        }
                    });
                });
                this.platAll = res.data;
            });
        },
        // 申请开通
        applyOpen(item) {
            let allHotel = JSON.parse(localStorage.getItem('c_hotelAll'));
            this.dataform_add.hotelName = allHotel.find((item) => item.id == this.$route.query.hotelId).hotelName;
            this.dataform_add.otaSource = item.dictValue;
            this.dataform_add.otaSourceName = item.dictLabel;
            this.show_apply = true;
        },
        // 编辑账号、密码
        confirmApply(action, done) {
            if (action == 'confirm') {
                if (!this.dataform_add.otaUserName) {
                    Toast('请输入账号');
                    done(false);
                    return;
                }
                if (!this.dataform_add.otaPassWord) {
                    Toast('请输入密码');
                    done(false);
                    return;
                }
                done(false);
                this.show_apply = false;
                PostBusinessHotelOtaAccountAdd(this.dataform_add).done((res) => {
                    Toast(res.msg);
                    done(false);
                    this.show_editDepart = false;
                    this.getStatus();
                });
            } else if (action == 'cancel') {
                done(false);
                this.show_apply = false;
            }
        },
        goNext(url, item) {
            this.$router.push({
                path: url,
                query: {
                    otaSource: item.dictValue,
                    otaSourceName: item.dictLabel,
                    hotelOtaRelationId: item.hotelOtaRelationId,
                    hotelId: this.$route.query.hotelId
                }
            });
        }
    }
};
</script>
<style scoped>
.scroll {
    position: fixed;
    top: 60px;
    bottom: 0;
    overflow-y: scroll;
    color: #000;
    width: 100%;
}
#plat_box {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: auto;
}
.content {
    margin: 10px;
    background: #fff;
    width: 44%;
    font-size: 14px;
    line-height: 30px;
    border-radius: 8px;
    min-height: 180px;
}
.aaa {
    width: 50%;
    margin: 8px auto;
    border-radius: 20px;
    color: #048fe5;
    background: #c2e4f9;
}
.bbb {
    width: 50%;
    margin: 8px auto;
    border-radius: 20px;
    color: #fff;
    background: #b1b1b1;
}
.ccc {
    width: 50%;
    margin: 8px auto;
    border-radius: 20px;
    color: #fff;
    background: #ff7d41;
}
.nothing {
    width: 50%;
    margin: 8px auto;
    border-radius: 20px;
    color: #fff;
    background: #b1b1b1;
}
</style>



